import React, {useState} from "react";
import * as styles from "./ChooseSquad.module.scss";
import SquadItem from "../../common/SquadItem/SquadItem";
import Button from "../../base/Button/Button";
import * as warBands from "../../../warBands.json";
import {Link} from "react-router-dom";
import {ROUTES} from "../../App";
import {useTranslation} from 'react-i18next'
import {useDispatch, useSelector} from 'react-redux'
import {gameActions} from '../../../store/gameReducer'
import {saveGame} from '../../../storage/localStorage'

const ChooseSquad = () => {
  const dispatch = useDispatch()
  const { t } = useTranslation()
  const [focusedSquad, setFocusedSquad] = useState(0);
  const bandItems = warBands.items;

  const handleAcceptChooseClick = () => {
    // dispatch(gameActions.resetGame())
  }

  const squadElements = bandItems.map(({name, icon, color}, i) => {
    return <SquadItem
      name={name}
      image={icon}
      color={color}
      key={`war-band-${i}`}
      isFocused={focusedSquad === i}
      onClick={() => {
        setFocusedSquad(i);
        dispatch(gameActions.setCurrentWarBand(i))
      }}
    />
  });

  return (
    <div className={styles.root}>
      <div className={styles.title}>{t("chooseSquad")}</div>
      <div className={styles.paper}>
        <div className={styles.items}>
          {squadElements}
        </div>
        <div className={styles.bottom}>
          <Link to={ROUTES.PLAY_FIELD.path}><Button onClick={handleAcceptChooseClick}>{t("acceptChoose")}</Button></Link>
        </div>
      </div>
    </div>
  )
};

export default ChooseSquad;