import React from "react";
import * as styles from "./InGameTabs.module.scss";
import {NavLink} from 'react-router-dom'
import {ROUTES} from "../../App";
import ButtonSecondary from "../../base/ButtonSecondary/ButtonSecondary";

const InGameTabs = () => {
  return (
    <div className={styles.root}>
      <NavLink activeClassName={styles.active} to={ROUTES.CARDS.path}><ButtonSecondary>Cards</ButtonSecondary></NavLink>
      <NavLink activeClassName={styles.active} to={ROUTES.PLAY_FIELD.path}><ButtonSecondary>Field</ButtonSecondary></NavLink>
      <NavLink exact activeClassName={styles.active} to={ROUTES.MAIN.path}><ButtonSecondary>Menu</ButtonSecondary></NavLink>
    </div>
  )
};

export default InGameTabs;