import React, {useEffect, useState} from 'react'
import * as styles from './Cards.module.scss'
import Button from '../../base/Button/Button'
import InGameTabs from '../../common/InGameTabs/InGameTabs'
import {getActiveActivationTokens} from '../../../utils/utils'
import {useDispatch, useSelector} from 'react-redux'
import {Card, gameActions} from '../../../store/gameReducer'
import * as warBands from '../../../warBands.json'
import FighterItem from '../../common/FighterItem/FighterItem'

const Cards = () => {
  const dispatch = useDispatch()
  const {rounds} = useSelector(({game}) => game)
  const {currentRound} = useSelector(({game}) => game)
  const {currentWarBand} = useSelector(({game}) => game)
  const [currentCardIndex, setCurrentCardIndex] = useState(0)
  const [isCardFlipped, setIsCardFlipped] = useState(false)
  const [isCardShownFullScreen, setIsCardShownFullScreen] = useState(false)

  const currentRoundObj = rounds[currentRound]
  const activationTokens = currentRoundObj?.activationTokens
  const activeActivationTokens = getActiveActivationTokens(activationTokens)
  const activeActivationTokensCount = activeActivationTokens.length

  const roundCards = rounds[currentRound]?.cards || []

  useEffect(() => {
    if (isCardFlipped) {
      setIsCardFlipped(false)
    }
  }, [currentCardIndex])

  const onHpControlButtonClick = (increase = true) => {
    if (increase) {
      dispatch(gameActions.updateCard(currentRound, currentCardIndex, {
        healthPoints: currentCardObj?.healthPoints + 1
      }))
    } else {
      const {healthPoints} = currentCardObj

      if (healthPoints) {
        dispatch(gameActions.updateCard(currentRound, currentCardIndex, {
          healthPoints: healthPoints - 1
        }))
      }
    }
  }

  const applySkill = (skillName) => {
    if (activeActivationTokensCount) {
      dispatch(gameActions.updateCard(currentRound, currentCardIndex, {
        [skillName]: {
          applied: true
        }
      }))

      let activationTokenActiveIndex

      for (let i = activationTokens.length - 1, length = 0; i >= length; i--) {
        if (activationTokens[i].isActive) {
          activationTokenActiveIndex = i
          break
        }
      }

      dispatch(gameActions.updateActivationToken(
        currentRound,
        activationTokenActiveIndex,
        {isActive: false })
      )
    }
  }
  const revokeSkill = (skillName) => {
    dispatch(
      gameActions.updateCard(currentRound, currentCardIndex, {
        [skillName]: false
      })
    )
  }

  const flipCard = () => {
    setIsCardFlipped(!isCardFlipped)
  }

  const toggleCardFullScreen = () => {
    setIsCardShownFullScreen(!isCardShownFullScreen)
  }

  const currentCardObj: Card = roundCards[0]
  let canApplyDefenceOrDodge
  let canApplyMoveAndAttack
  let canApplyMove
  let canRevokeDefenceOrDodge
  let canRevokeMoveAndAttack
  let canRevokeMove

  if (currentCardObj) {
    const {move, attack, defend, dodge} = currentCardObj

    canApplyDefenceOrDodge = activeActivationTokensCount && !defend.applied
      && !dodge.applied && !attack.applied && !move.applied;
    canApplyMoveAndAttack = activeActivationTokensCount && !attack.applied && !move.applied;
    canApplyMove = activeActivationTokensCount && !attack.applied && !move.applied;
    canRevokeDefenceOrDodge = defend.applied && dodge.applied
    canRevokeMoveAndAttack = defend.applied && dodge.applied && attack.applied && move.applied;
    canRevokeMove = move.applied
  }

  const {fighters} = warBands.items[currentWarBand];

  const cardElements = fighters.map(({icon, name}, i) => {
    return <FighterItem
      key={`fighter-${i}`}
      name={name}
      image={icon}
      className={styles.characterItem}
      isFocused={currentCardIndex === i}
      onClick={() => {
        setCurrentCardIndex(i)
      }}
    />
  })
  const {name, description, card} = fighters[currentCardIndex]

  let cardImageSrc
  if (isCardFlipped) {
    cardImageSrc = card.flipped
  } else {
    cardImageSrc = card.default
  }

  return (
    <div className={styles.root}>
      <div className={styles.top}>
        <div className={styles.characters}>
          {cardElements}
        </div>
      </div>
      <div className={styles.middle}>
        {
          (currentCardObj)
            ? <>
              <div className={styles.card}>
                <div className={styles.left}>
                  <Button theme="secondary" onClick={flipCard}>Turn over the card</Button>
                  <Button theme="secondary" disabled={!canRevokeMove && !canRevokeMoveAndAttack} onClick={() => {
                    revokeSkill('move')
                    revokeSkill('attack')
                  }}>Revoke Attack + Move or Move
                  </Button>
                  <Button theme="secondary" disabled={!canRevokeDefenceOrDodge} onClick={() => {
                    revokeSkill('defend')
                    revokeSkill('dodge')
                  }}>Revoke Defense or dodge</Button>
                </div>
                <div className={styles.cardContainer}>
                  <img onClick={toggleCardFullScreen} src={cardImageSrc} alt={name}/>
                </div>
                <div className={styles.right}>
                  <Button
                    onClick={() => {
                      onHpControlButtonClick(true)
                    }}
                    theme="secondary"
                  >+</Button>
                  <div className={styles.healthPoints}>{currentCardObj.healthPoints}</div>
                  <Button
                    disabled={!currentCardObj.healthPoints}
                    onClick={() => {
                      onHpControlButtonClick(false)
                    }}
                    theme="secondary"
                  >-</Button>
                </div>
              </div>
              <div className={styles.actions}>
                <Button
                  theme="secondary"
                  disabled={!canApplyDefenceOrDodge}
                  onClick={() => {
                    applySkill('defend')
                    applySkill('dodge')
                  }}
                >Defense or dodge</Button>
                <Button
                  theme="secondary"
                  disabled={!canApplyMoveAndAttack}
                  onClick={() => {
                    applySkill('move')
                    applySkill('attack')
                  }}
                >Attack + Move</Button>
                <Button
                  theme="secondary"
                  disabled={!canApplyMove}
                  onClick={() => {
                    applySkill('move')
                  }}
                >Move</Button>
              </div>
            </>
            : ''
        }
      </div>
      <div className={styles.bottom}>
        <InGameTabs/>
      </div>

      {
        (isCardShownFullScreen)
          ? (
            <div className={styles.cardFullScreenContainer}>
              <p>{name}</p>
              <div className={styles.imageContainer}>
                <img onClick={toggleCardFullScreen} src={cardImageSrc} alt={name}/>
              </div>
              <p>{description}</p>
            </div>
          )
          : ""
      }
    </div>
  )
}

export default Cards