import React, {ButtonHTMLAttributes, DetailedHTMLProps, FC} from 'react'
import * as classes from './Icons.module.scss'
import classnames from 'classnames'
import {makeImageIconUrl} from '../../utils/utils'

type ImageButtonProps = {
  srcDisabled?: string
  srcHover?: string
  srcDefault: string
} & DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>

const ImageButton: FC<ImageButtonProps> = ({ srcDisabled, srcHover, srcDefault, className, ...restProps}) => {
  return (
    <button
      className={classnames(classes.root, className)}
      style={{
        '--src-disabled': `url("${makeImageIconUrl(srcDisabled || srcDefault)}")`,
        '--src-hover': `url("${makeImageIconUrl(srcHover || srcDefault)}")`,
        '--src-default': `url("${makeImageIconUrl(srcDefault)}")`
      }}
      {...restProps}
    >
      <div className={classes.imageContainer}/>
    </button>
  )
}

export default ImageButton