import React from "react";
import * as styles from "./InnerBorderRadiusWrapper.module.scss";
import * as classnames from 'classnames'

const InnerBorderRadiusWrapper = ({className, children, ...restProps}) => {
  return (
    <div className={classnames(styles.outer, className)} {...restProps}>
      <div className={styles.inner}>
        <i className={classnames(styles.top, styles.left)}/>
        <i className={classnames(styles.top, styles.right)}/>
        <div className={styles.content}>
          {children}
        </div>
        <i className={classnames(styles.bottom, styles.right)}/>
        <i className={classnames(styles.bottom, styles.left)}/>
      </div>
    </div>
  )
};

export default InnerBorderRadiusWrapper;